<template>
  <div class="main">
    <h1>Alcohol shop</h1>

    <div>
      <div>
        <el-button type="info" @click="enableBuyingItems">Buy items</el-button>
        <el-button type="info" @click="enableSeeingItemsBought"
          >Shopping cart</el-button
        >
      </div>
      <div class="toBeBought" v-if="itemsToBeBoughtPage">
        <el-checkbox-group v-model="itemsToBeBought">
          <el-checkbox
            v-for="(item, index) in shopItems"
            :label="item"
            :key="index"
            ><el-row>
              <el-card :body-style="{ padding: '0px' }">
                <img :src="item.image" class="image" />
                <div style="padding: 14px">
                  <span>{{ item.name }}</span>
                  <div class="bottom clearfix"></div>
                </div>
              </el-card>
            </el-row>
          </el-checkbox>
          <el-button
            class="buyButton"
            type="success"
            icon="el-icon-message"
            circle
            @click="buyItems"
            >Buy</el-button
          >
        </el-checkbox-group>
      </div>
    </div>
    <div class="shopping cart" v-if="shoppingCartPage">
      <el-table :data="itemsBought" border style="width: 90%" class="table">
        <el-table-column prop="name" label="name"> </el-table-column>
        <el-table-column prop="price" label="price"> </el-table-column>
        <el-table-column fixed="right" label="Operations">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, itemsBought)"
              type="text"
              size="small"
            >
              Remove item
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// import ToBeBought from "./ToBeBought.vue";

export default {
  name: "AlcoholShop",

  components: {
    // ToBeBought,
  },
  data() {
    return {
      itemsBought: [],
      itemsToBeBought: [],
      itemsToBeBoughtPage: true,
      shoppingCartPage: false,
      shopItems: [
        {
          name: "wine",
          price: 20,
          image:
            "data:image/jpeg;base64,",
        },
        {
          name: "beer",
          price: 3,
          image:
            "data:image/jpeg;base64,",
        },
        {
          name: "whiskey",
          price: 30,
          image:
            "data:image/jpeg;base64,",
        },
        {
          name: "gin",
          price: 15,
          image:
            "data:image/jpeg;base64,",
        },
        {
          name: "vodka",
          price: 25,
          image:
            "data:image/jpeg;base64,",
        },
      ],
    };
  },
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    buyItems() {
      this.itemsBought.push(...this.itemsToBeBought);
      console.log("@@@" + this.itemsBought);
      this.tooglePages();
    },
    tooglePages() {
      this.itemsToBeBoughtPage = !this.itemsToBeBoughtPage;
      this.shoppingCartPage = !this.shoppingCartPage;
    },
    enableBuyingItems() {
      this.itemsToBeBoughtPage = true;
      this.shoppingCartPage = false;
    },
    enableSeeingItemsBought() {
      this.itemsToBeBoughtPage = false;
      this.shoppingCartPage = true;
    },
  },
};
</script>

<style scoped>
h1 {
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
  text-decoration: underline;
}
.main {
  background-color: aliceblue;
  width: 70vw;
  height: 90vh;
  border-radius: 5%;
  opacity: 94%;
}
.toBeBought {
  margin-top: 20px;
  font-size: 20px;
  text-decoration: none;
}
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  margin-top: 5%;
  margin-left: 15%;
  width: 70px;
  height: 70px;
  display: block;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
.table {
  margin: 30px;
}
.buyButton {
  margin-top: 20px;
  align-self: center;
  display: block;
  margin-left: calc(50% - 50px);
  height: 60px;
  width: 100px;
}
</style>
